import React, {Component} from "react";
import {OraComplexRadio} from "eshop/components/OraCommonComponents";
export class MobileBillingAccountTitle extends Component {

    constructor(props) {
        super(props);
    }

    useAccount(e) {
        if(e.target.value==this.props.account.accountId) {
            this.props.setSelectedBillingAccount(this.props.account.accountId, this.props.account.accountCode);
        }
    }
    getPropsForRadio(text, value) {

        return {
            name: "accountSelect",
            text: text,
            value: value,
            labelClassName: "o-radio opl-radio",
            readOnly: false,
            checked:value==this.props.selectedBillingAccountId,
            onChange:this.useAccount.bind(this)
        };
    }
    render() {
        let label=(this.props.account && this.props.account.groupName ? "Konto mobilne z "+this.props.account.groupName:"Konto mobilne");

        return (
            <div className="u-margin-m">
                <OraComplexRadio    {...this.getPropsForRadio(label,this.props.account.accountId)} >
                    <p className=""> {label}</p>
                    <p className="g-gray5-c u-font-normal">{this.props.account.accountCode}</p>
                </OraComplexRadio>
            </div>
        )
   }
}
